import React from 'react';
import ReactDOM from 'react-dom';
import {Header} from './Header/Header';
import {WritingText} from './Writing&Title/WritingText';

//header
let navbarList = ['Projects', 'About', 'Contact'];
ReactDOM.render(
    <Header list = {navbarList} name = "AMRITPAL CHERA" TitleHref = "/"/>,
    document.getElementById('header')
);

//title with text (template for 10 things)
let aboutText = (
  'An ambitious and an entrepreneurial mind who keeps his principles always as the priority. My time is my biggest asset. My name is Amritpal Chera; currently a candidate for Bachelor’s of Mechatronics Engineering at the University of Waterloo. I have visions of changing the world one day and I am working passionately to make them come true. My origins are from a small village in Punjab, India, which to my advantage give me a different perspective on this world than my current technology reliant generation. I believe in innovation to improve the world, a world where comfort is accompanied by knowledge. Without knowledge, even one’s comfort can become a disadvantage over the long run.'
)


ReactDOM.render(
    <WritingText title ="ABOUT ME" text={aboutText}  subTitle = "Who Am I?:" TitleHref = "/about"/>,
    document.getElementById('aboutPage')
);

