import React from 'react';
import '../styles.css';

export const Title = (props)=>{
    let toReturn = null;
    props.subTitle? toReturn = (<div className = "subtitleSubpage" href = {props.href}>{props.subTitle} </div>):
    toReturn = (<div className= "titleSubpage" href = {props.href}>{props.name}</div>);
    return (toReturn);
}